/* General Styling */

html {
  background-color: rgb(220, 220, 220);
  background-image: url("https://preview.redd.it/rzlsu46nn5c71.jpg?width=630&format=pjpg&auto=webp&s=f0c5c3c0635256888bc5a512d863427d3f9ef089");
  background-repeat: repeat;
  background-size: 13rem;
  display: flex;
  flex: 1;
}

html,
body,
#root {
  min-height: 100vh;
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
}

.container {
  flex-grow: 1;
}

.container > * {
  flex-shrink: 0;
  min-width: fit-content;
}

.flex-nav,
.container > footer {
  margin: 0;
  padding: 0;
  background-color: black;
  color: rgb(198, 198, 198);
}

main {
  flex-grow: 1;
  filter: drop-shadow(0 4mm 4mm rgb(23, 0, 0));
}

.product-name {
  font-size: xx-large;
  text-transform: capitalize;
}

.flex-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.flex-container {
  display: flex;
  text-align: center;
}

.container,
form {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.flex-text-img {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  width: 100%;
}

.flex-text-img > img {
  width: 25%;
}

.container > form {
  width: 100%;
  gap: 0.5rem;
}

.input-container {
  display: flex;
  gap: 0.5rem;
  width: 100%;
}

.input-container > label {
  width: 4rem;
}

.input-container > input {
  flex-grow: 1;
}

.flex-center-gap {
  display: flex;
  justify-content: center;
  gap: 5%;
}

.centered {
  margin: 0.9rem 0;
  margin-right: auto;
  margin-left: auto;
  padding: 1rem;
  border-radius: 2rem;
  background-color: black;
  min-width: 60%;
  max-width: 10rem;
}

.centered > div {
  background-color: rgb(255, 255, 255);
  padding: 1rem;
  border-radius: 1rem;
  min-height: 90%;
  min-width: max(15rem, 80%);
}

.center-text {
  text-align: center;
}

/* Nav styling */

header {
  position: sticky;
  z-index: 1;
  top: 0;
  filter: drop-shadow(0 4mm 4mm rgb(23, 0, 0));
}

.flex-nav {
  align-items: center;
  justify-content: center;
  line-height: baseline;
}

.flex-nav > li {
  padding: 0rem 0.5rem;
  margin: 0.5rem 1rem;
  font-weight: bold;
  border-radius: 0.1rem;
}

h1,
h2,
nav svg:hover,
nav a:hover,
.flex-nav .active {
  user-select: none;
  color: rgb(255, 0, 0);
}

a:hover,
button:hover,
button > *:hover {
  cursor: pointer;
}

.flex-pair > button {
  all: unset;
}

.flex-pair {
  display: flex;
  flex-grow: 1;
  gap: 1rem;
}

.flex-pair > input {
  flex-grow: 1;
}

.flex-end {
  text-align: end;
  justify-self: flex-end;
}

.bag-pair {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.flex-nav svg {
  width: 1.5rem;
}

.bag-pair > span {
  position: absolute;
  z-index: 1;
  margin-top: 15%;
}

/* Footer styling */

.flex-footer {
  display: flex;
  z-index: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  gap: 0.1rem;
}

.flex-footer > * {
  display: flex;
  gap: 0.5rem;
  padding: 0.25rem 0;
}

.flex-footer a,
.flex-nav a {
  all: unset;
  cursor: pointer;
}

.flex-footer a:hover {
  text-decoration: underline;
}

/* Products & Shop */

.remove-child-padding {
  margin: 1rem 0 2rem 0;
}

.remove-child-padding > * {
  padding: 0;
  margin: 0;
}

.subheading {
  color: rgb(61, 61, 61);
}

.tiled {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.tiled .product-card img:hover {
  height: 99%;
}

.tiled > * {
  all: unset;
  width: 15rem;
  height: fit-content;
  padding: 0;
  margin: 0;
}

.product-page {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
  align-items: center;
}

.product-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.product-card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  font-weight: bold;
  text-transform: capitalize;
}

.product-card > div {
  height: 100%;
  width: 100%;
}

.product-card div > img {
  transition-timing-function: ease-in-out;
  transition: 0.3s;
  aspect-ratio: 1/1;
  height: 90%;
  width: 90%;
}

.product-details {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.product-details img {
  max-width: 15rem;
}

.product-sale {
  color: red;
}

.text-strike {
  color: rgb(61, 61, 61);
  text-decoration: line-through;
}

.product-counter > button,
.product-variation > select {
  all: unset;
  cursor: pointer;
  padding: 0 0.5rem;
  font-weight: bold;
}

.product-counter > * {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.product-counter > button {
  flex-grow: 1;
  height: 100%;
}

.product-counter > button:active {
  background-color: rgba(0, 0, 0, 0.2);
}

.product-variation > select {
  margin-left: 0.5rem;
}

.product-counter,
.product-variation > select {
  user-select: none;
  text-align: center;
  align-items: center;
  justify-content: center;
  outline: 1px solid black;
  border-radius: 0.3rem;
  gap: 0.5rem;
}

.product-counter {
  font-family: monospace;
  padding: 0;
  margin: 0;
}

.product-table {
  margin-top: 10%;
  width: 100%;
  border-collapse: collapse;
}

.product-table tr {
  border: 1px solid rgba(0, 0, 0, 0.6) !important;
}

.product-table tr td {
  padding: 0.5rem 1rem;
}

.product-table * {
  height: 100%;
}

.product-table .product-counter {
  outline: none !important;
}

.product-table > caption {
  font-size: x-large;
  margin-bottom: 0.5rem;
}

.product-table img {
  width: 5rem !important;
  padding: 0.5rem;
}

.product-table a {
  all: unset;
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
}

.product-table a:hover {
  text-decoration: underline;
}

.faded {
  width: 100%;
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.35),
    rgba(0, 0, 0, 0)
  );
}

.cart-summary {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 2rem 1rem;
}

.cart-summary h3 {
  padding: 0;
  margin: 0;
}

@media only screen and (max-width: 600px) {
  /* nav */
  nav > ul {
    display: flex;
    justify-content: flex-start;
  }

  nav > ul > li {
    padding: 0;
    margin: 0;
  }

  nav > ul > li:first-child {
    display: none;
  }

  /* body */
  .centered {
    padding: 0;
    margin: 0;
    min-width: 100%;
    border-radius: 0;
  }

  .centered > div {
    margin: 0;
    border-radius: 0;
  }
}
